import JXObject from "jx/core/JXObject";
import Common from "jx/editor/editionitems/Common";
let textInput = `
<div class="form-group row">
    <div class="row">
      <div class="col-xs-5">
          <div class="input-group">
              <span id="check" class="input-group-addon"><span class="fa fa-font"></span></span>
              <input type="text" class="form-control" id="textSound" value="ID" readonly="true" >
          </div>
      </div>
      <div class="col-xs-1">
           <button class="btn btn-default" title="Informations" id="info"><span class="fa fa-info"></span></button>
      </div>
   <div class="col-xs-6">
        <textarea id="value" type="textarea" rows="1" class="form-control">$VALUE</textarea>
   </div>
   </div>
      <div  id="informationsPanel" class="row"  style="display:none;">
          <div class="col-xs-12" id="informationsText">
          <div class"xinputtext" id="description" ></div>

          </div>
      </div>
</div>
`;
class Text extends JXObject {
    constructor(controller, params) {
        super();
        this.controller = controller;
        this.commonLib = new Common({ jx: this.jx });
        var item = $(textInput);
        item.prop('id', params.id);
        //if (params.description == undefined) { item.remove($(item.description_btn)); }
        item.find("#description").text(params.description || "");
        item.find("#value").prop('readonly', (params.editable != undefined) ? !params.editable : false);
        item[0].addEventListener("change", (event) => { this.controller.onChangeData(event); });
        item.html(item[0].innerHTML.replace("ID", params.id + " (" + params.type + ")").replace("$VALUE", "default: " + (params.value || "")).replace("$description", params.description || ""));
        item.find("#info").click((event) => {
            console.log("info", event.target, item[0].id);
            var panelInfo = item.find("#informationsPanel");
            panelInfo.css("display", (panelInfo.css("display") == "none" ? "inline" : "none"));
        });
        this.item = item;
    }
}
export default Text;
